import { Routes } from '@angular/router';

export const APP_STORE_ROUTES: Routes = [
  {
    path: '',
    loadComponent: () =>
      import('./pages/app-store-shell/app-store-shell.component').then(
        (m) => m.AppStoreShellComponent,
      ),
    children: [
      {
        path: '',
        loadComponent: () =>
          import('./pages/app-marketplace/app-marketplace.component').then(
            (m) => m.AppMarketplaceComponent,
          ),
      },
      {
        path: 'my-apps',
        loadComponent: () =>
          import('./pages/my-installed-apps/my-installed-apps.component').then(
            (m) => m.MyInstalledAppsComponent,
          ),
      },
      {
        path: 'my-created-apps',
        loadComponent: () =>
          import(
            './pages/app-creator-dashboard/app-creator-dashboard.component'
          ).then((m) => m.AppCreatorDashboardComponent),
      },
      {
        path: 'admin',
        loadComponent: () =>
          import('./pages/app-admin-panel/app-admin-panel.component').then(
            (m) => m.AppAdminPanelComponent,
          ),
      },
    ],
  },
  {
    path: 'editor',
    loadComponent: () =>
      import('./pages/app-create-wizard/app-create-wizard.component').then(
        (m) => m.AppCreateWizardComponent,
      ),
  },
  {
    path: ':id',
    loadComponent: () =>
      import('./pages/app-detail/app-detail.component').then(
        (m) => m.AppDetailComponent,
      ),
  },
];
